import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import './ui.less';

var Waiting = React.createClass({
    render: function() {
        var waiting = this.props.waiting;
        var mask = waiting < 2 ? <div className="ui-mask"/> : null;

        return waiting ? (
            <div id="ui-waiting">
                {mask}
                <p className="ui-loading"/>
            </div>
        ) : null;
    }
});

var Toast = React.createClass({
    render: function() {
        var toast = this.props.toast;

        return toast ? (
            <div id="ui-toast" className={toast ? '' : 'none'}>
                <p>{toast}</p>
            </div>
        ) : null;
    }
});

// action sheet
var setMenuState;
var Menu = React.createClass({
    getInitialState: function() {
        return {
            state: 0,
            btns: []
        };
    },
    componentDidMount: function() {
        setMenuState = this.setState.bind(this);
    },
    handleClick: function(i) {
        this.setState({ state: 0});
        if(this.state.action) this.state.action(i);
    },
    render: function() {
        var that = this;
        var body = null;

        if(this.state.state){
            var btns = this.state.btns.map(function(item, i){
                var btn = {__html: item};

                return <li key={i} onClick={() => that.handleClick(i)} dangerouslySetInnerHTML={btn}/>;
            });

            body = (
                <div id="ui-menu">
                    <div className="ui-menus">
                        <ul>{btns}</ul>
                        <ul><li onClick={() => this.handleClick(-1)}>取消</li></ul>
                    </div>
                    <div className="ui-mask" onClick={() => this.handleClick(-1)}/>
                </div>
            );
        }

        return <ReactCSSTransitionGroup transitionName="ui-menu" transitionEnterTimeout={400} transitionLeaveTimeout={400} component="div">{body}</ReactCSSTransitionGroup>;
    }
});

var setDialogState;
var Dialog = React.createClass({
    getInitialState: function() {
        return {
            state: 0,
            title: '',
            cnt: '',
            btns: []
        };
    },
    componentDidMount: function() {
        setDialogState = this.setState.bind(this);
    },
    handleClick: function(i) {
        this.setState({ state: 0});
        if(this.state.action) this.state.action(i);
    },
    render: function() {
        var that = this;
        var btns = this.state.btns;

        // 最后一个按钮高亮
        btns = btns.map(function(item, i){
            return <a key={i} onClick={() => that.handleClick(i)} className={i+1 == btns.length ? 'primary' : ''}>{item}</a>;
        });

        return (
            <div id="ui-dialog" className={this.state.state ? 'on' : ''}>
                <div className="ui-mask dark"/>
                <div className="body">
                    <h2>{this.state.title}</h2>
                    <p>{this.state.cnt}</p>
                    <div className="actions">{btns}</div>
                </div>
            </div>
        );
    }
});


var setState;
export var UI = React.createClass({
    getInitialState: function() {
        var that = this;

        return {
            waiting: 0,
            toast: '',

            menuState: 0,
            menuBtns: [],
            menuAction: function(){}
        };
    },
    componentDidMount: function() {
        setState = this.setState.bind(this);
    },
    render: function() {
        var state = this.state;

        return (
            <div>
                <Waiting waiting={state.waiting}/>
                <Toast toast={state.toast}/>
                <Menu/>
                <Dialog/>
            </div>
        );
    }
});

export default window.ui = {
    showWaiting: function(state){
        setState({ waiting: state || 1 });
    },
    closeWaiting: function() {
        setState({ waiting: 0 });
    },
    toast: (function() {
        var tid;

        return function(msg) {
            if(tid) {
                clearTimeout(tid);
                tid = 0;
            }

            // show toast
            setState({ toast: msg});

            // close toast
            tid = setTimeout(function() {
                setState({ toast: ''});
            }, 3e3);
        }
    })(),

    menu: function(options){
        setMenuState(Object.assign({ state: 1}, options));
    },

    dialog: function(options){
        setDialogState(Object.assign({ state: 1}, options));
    }
};



