import React from 'react';
import { render } from 'react-dom';
import { UI } from 'src/ui';

import './app.less';
import View from './view';

var App = React.createClass({
    render: function(){
        return (
            <div>
                <UI/>
                <View/>
            </div>
        );
    }
});

render(<App/>, document.getElementById('app'));



